import {CosmeticItemsModelDB} from '../database';
export const getErrorValidation = (code:number) => {
    switch (code) {
        case 0:
            return "  ";
        case 1:
            return "    ";
        case 2:
            return "   ";
        default:
            return "  ";
    }
};

export const isNotEmpty = (x: string) => (!!x ? 0 : 1);
export const alreadyIdExistsInDB =(id:string)=>{
    return CosmeticItemsModelDB.get(id)
}